

















export default {
	data() {
		return {
			info:{},
			time:''
		};
	},
	onLoad(){
		this.info=this.$store.state.globalData.userInfo.member
		this.getWithdrawTime()
	},
	methods:{
		getWithdrawTime() {
			var that = this
			this.$api.get('/api/common/getContent', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.time = res.data.data.coin_withdraw_times
				}
			})
		},
	},
}
